* {
  margin: 0;
  padding: 0;
}
:host {
  display: flex;
  margin-bottom: 5px;
  gap: 5px;
  width: 100%;
}
strong {
  margin: 0;
  padding: 0;
}
ul {
  margin: 0 3px;
  padding: 0;
  list-style: none;
  overflow: hidden;
  width: 100%;
  word-wrap: break-word !important;
  overflow-wrap: break-word !important;
}
li {
  width: 100%;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  word-wrap: break-word !important;
  overflow-wrap: break-word !important;
}

button {
  background: white;
  color: var(--blue);
  padding: 0;
  margin: 0;
  margin-left: 3px;
  font-size: 12px;
  border: 0;
}
button:hover {
  color: #355270;
}
